.display {
  font-family: 'Cutive Mono', monospace;
  width: 400px;
  height: 100px;
  margin: 25px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  box-sizing: border-box;
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: inset 0 0 10px 0 black;
}
.display-numbers {
  width: 100%;
  margin: 0 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.display--on {
  background-color: lightgreen;
}
.display--off {
  background-color: lightgrey;
}
